import React from 'react';
import { Link } from 'react-router-dom';
import data_projets from '../data/data_projets';

export default class TSProjets extends React.Component {

  componentDidMount() {
    this.props.area && this.props.area.scrollTo(0,0);
  }

  render() {

    const lang = this.props.language;

    return (
      <div className="ts_projets table">
        <div className="tr thead">
          <div className="th"><div><span>{lang === 'fr' ? 'nom' : 'name'}</span></div></div>
          <div className="th"><div><span>{lang === 'fr' ? 'fait par' : 'made by'}</span></div></div>
          <div className="th"><div><span>{lang === 'fr' ? 'fait pour' : 'made for'}</span></div></div>
          <div className="th"><div><span>{lang === 'fr' ? 'localisation' : 'location'}</span></div></div>
          <div className="th"><div><span>{lang === 'fr' ? 'fonction' : 'function'}</span></div></div>
          <div className="th"><div><span>{lang === 'fr' ? 'surface plancher' : 'floor area'}</span></div></div>
          <div className="th"><div><span>{lang === 'fr' ? 'phase' : 'phase'}</span></div></div>
          <div className="th"><div><span>{lang === 'fr' ? 'matière' : 'material'}</span></div></div>
        </div>
            {Object
              .keys(data_projets)
              .map((key, index) => 
                <div className={`tr tr${index}`} key={index}>
                    <div className="td td__name"><Link to={data_projets[key].url}>{data_projets[key].name}</Link></div>
                    <div className="td">{data_projets[key][lang].madeBy}</div>
                    <div className="td">{data_projets[key][lang].madeFor}</div>
                    <div className="td">{data_projets[key][lang].city}<br/>{data_projets[key][lang].departmentNumber}<br/>{data_projets[key][lang].country}</div>
                    <div className="td">{data_projets[key][lang].function}</div>
                    <div className="td td__surface">{data_projets[key].surface}m<sup>2</sup></div>
                    <div className="td">{data_projets[key][lang].phase}</div>
                    <div className="td">{data_projets[key][lang].material}</div>
                </div>
              )}
        <div className="tr tr__empty">
            <div className="td"></div>
            <div className="td"></div>
            <div className="td"></div>
            <div className="td"></div>
            <div className="td"></div>
            <div className="td"></div>
            <div className="td"></div>
            <div className="td"></div>
        </div>
      </div>

      )
  }
}

TSProjets.propTypes = {
  //*propName*: React.PropTypes.*proptype*.*isRequired*,
}



