import React from 'react';
import { Link } from 'react-router-dom';
import data_projets from '../data/data_projets';

export default class Projets extends React.Component {

  componentDidMount() {
    this.props.area && this.props.area.scrollTo(0,0);
  }

  render() {

    const lang = this.props.language;


    return(
        <div className="projets">
          {Object
            .keys(data_projets)
            .sort((a, b) => data_projets[a].delivery > data_projets[b].delivery ? -1 : 1)
            .map((key, index) => 
              <div key={index} className="material">
                <Link className="material__container" to={data_projets[key].url}>
                  <img 
                    src={data_projets[key].img}
                    alt={data_projets[key][lang].material}
                    style={{ 
                      height: `${data_projets[key].materialPicHeight * 10}px`,
                      // width: '100%',
                      // backgroundImage: `url(${data_projets[key].img})`,
                      objectFit: 'cover'}}>
                  </img>
                  <div className="material__container--overlay">
                    {this.props.language === 'fr' ? 'cliquez pour découvrir le projet' : 'click to discover the project'}
                  </div>
                </Link>
                <div className="material__projet--container">
                  <div className="material__projet--content">
                    <Link to={data_projets[key].url} className="material__projet--headline">{key} {data_projets[key].win.getFullYear()}</Link>
                    <span className="material__projet--title" 
                          dangerouslySetInnerHTML={data_projets[key][lang].title}>
                    </span>
                    <span className="material__projet--location">{data_projets[key][lang].city}, {data_projets[key][lang].departmentName}, {data_projets[key][lang].country}
                    </span>
                    <span className="material__projet--surface">{data_projets[key].surface} m<sup>2</sup></span>                          
                    <span className="material__projet--delivery">{this.props.language === 'fr' 
                                                    ? 'Livraison'
                                                    : 'Delivery'} {this.props.language === 'fr' 
                                                    ? data_projets[key].delivery.toLocaleDateString('fr-FR', { month: 'long' })
                                                    : data_projets[key].delivery.toLocaleDateString('en-US', { month: 'long' })} {data_projets[key].delivery.getFullYear()}
                    </span>                          
                  </div>
                </div>
              </div>
            )}
        </div>
      )
  }
}

Projets.propTypes = {
  //*propName*: React.PropTypes.*proptype*.*isRequired*,
}





