import React from 'react';
import { Switch, Route, Redirect } from 'react-router';
import Projets from './Projets';
import TSProjets from './TSProjets';
import News from './News';
import Agence from './Agence';
import Presse from './Presse';

export default class Content extends React.Component {


  constructor (props) {
    super(props);
    this.state = {
      language: 'fr'
    };
  }

  render() {

    return(
        <div className="contentGrid">
          <div className="contentArea" ref={area => this.area = area}>
          <Switch>
            <Route exact path='/' render={() => <Redirect to='/projets' />} />
            <Route exact path='/projets' render={() => <Projets language={this.state.language} area={this.area} />} />
            <Route exact path='/ts_projets' render={() => <TSProjets language={this.state.language} area={this.area} />} />
            <Route exact path='/news' render={() => <News language={this.state.language} area={this.area} />} />
            <Route exact path='/agence' render={() => <Agence language={this.state.language} area={this.area} />} />
            <Route exact path='/presse' render={() => <Presse language={this.state.language} area={this.area} />} />
          </Switch>
          </div>
        </div>
      )
  }
}

Content.propTypes = {
  //*propName*: React.PropTypes.*proptype*.*isRequired*,
}



